  .msger {
    display: flex;
    flex-flow: column wrap;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    background: #fff;
  }
  
  .msger-header {
    position: relative;
    height: 50px;
    padding: 0;
    border-bottom: 1px solid rgb(201, 201, 201);
    background: #fff;
  }

  .mshger-info {
    margin: 15px 0 0 10px;
    font-size: 20px;
  }
  
  .msger-chat {
    flex: 1;
    overflow-y: auto;
    padding: 10px;
    background-color: white;
  }

  .msger-chat::-webkit-scrollbar {
    width: 3px;
  }

  .msger-chat::-webkit-scrollbar-track {
    background: #ddd;
  }

  .msger-chat::-webkit-scrollbar-thumb {
    background: #bdbdbd;
  }


  .msg {
    margin-bottom: 15px;
  }
  
  .media-left {
    display: block;
    float: left;
  }
  
  .img-sm {
    width: 70px;
    height: 70px;
  }
  
  .img-circle {
    border-radius: 50%;
  }
  
  .media-body {
    display: block;
    overflow: hidden;
    width: auto;
  }
  
  .pad-hor {
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 15px;
  }
  
  .speech {
    position: relative;
    background: #b7dcfe;
    color: #317787;
    display: inline-block;
    border-radius: 0;
    padding: 12px 20px;
  }
  
  .speech:before {
    content: "";
    display: block;
    position: absolute;
    width: 0;
    height: 0;
    left: 0;
    top: 0;
    border-top: 7px solid transparent;
    border-bottom: 7px solid transparent;
    border-right: 7px solid #b7dcfe;
    margin: 15px 0 0 -6px;
  }
  
  .speech .media-heading {
    font-size: 1.2em;
    color: #317787;
    display: block;
    border-bottom: 1px solid rgba(0,0,0,0.1);
    margin-bottom: 10px;
    padding-bottom: 5px;
    font-weight: 300;
    text-decoration: none;
  }
  
  .speech .message-text{
    margin: 0 0 10px;
  }
  
  .speech-time {
    margin-top: 20px;
    margin-bottom: 0;
    font-size: .8em;
    font-weight: 300;
  }
  
  .fa-fw {
    width: 1.28571429em;
    text-align: center;
  }
  
  .fa {
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    transform: translate(0, 0);
  }
  
  .fa-clock-o:before {
    content: "\f017";
  }
  
  .media-right {
    float: right;
  }
  
  .speech-right {
    text-align: right;
  }
  
  .speech-right>.speech {
    background: #ffda87;
    color: #a07617;
    text-align: right;
  }
  
  .speech-right>.speech:before {
    left: auto;
    right: 0;
    border-top: 7px solid transparent;
    border-bottom: 7px solid transparent;
    border-left: 7px solid #ffdc91;
    border-right: 0;
    margin: 15px -6px 0 0;
  }
  
  .speech-right>.speech .media-heading {
    color: #a07617;
  }


  
  .msger-inputarea {
    display: flex;
    padding: 10px;
    border-top: 2px solid #ddd;
    background: #eee;
  }
  .msger-inputarea * {
    padding: 10px;
    border: none;
    font-size: 1em;
  }

  .msger-input {
    flex: 1;
  }

  .msger-send-btn {
    margin-left: 10px;
    background: rgb(0, 196, 65);
    color: #fff;
    font-weight: bold;
    cursor: pointer;
    transition: background 0.23s;
    width: 100px;
    outline: none;
  }

  .msger-send-btn:hover {
    background: rgb(0, 180, 50);
  }
  
  /* ------------------------------------------------ */
  .dropbtn {
    background-color: #4CAF50;
    color: white;
    padding: 16px;
    font-size: 16px;
    border: none;
  }

  .dropdown {
    position: relative;
    display: inline-block;
  }

  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 190px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
  }

  .dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }

  .dropdown-content img {
    width: 60px;
    height: 60px;
  }

  .dropdown-content img:hover {
    border: 1px solid red;
  }

  .icon-active {
    border: 1px solid red;
  }

  .dropdown-content a:hover {background-color: #ddd;}

  .dropdown:hover .dropdown-content {display: block;}

  .dropdown:hover .dropbtn {background-color: #3e8e41;}
